<template>
  <div class="login_container">
   <div class="login_box">
       <!-- 头像区 -->
    <div class="avatar_box">
       <img src="../assets/logo.png" alt="">
    </div>
    <!-- 登录票单曲 -->
    <el-form :model="loginFrom" ref="loginFromRef" :rules="loginFromRules" label-width="0px" class="login_from">
        <!-- 用户名 -->
  <el-form-item prop="username" >
    <el-input v-model="loginFrom.username"  prefix-icon="el-icon-user-solid"></el-input>
  </el-form-item>
  <!-- 密码 -->
  <el-form-item prop="password" >
    <el-input v-model="loginFrom.password" type="password" prefix-icon="el-icon-shopping-bag-2"></el-input>
  </el-form-item>
  <!-- 按钮区 -->
   <el-form-item  class="btns" >
  <el-button type="primary" @click="login">登录</el-button>
  <el-button type="info" @click="resetLoginFrom">重置</el-button>
  </el-form-item>
    </el-form>
   </div>
  </div>
</template>

<script>
export default {
  data(){
      return{
        //   这是登录表单的数据绑定对象
        loginFrom:{
            username:'',
            password:'',
        },
        // 表单验证规则对象
        loginFromRules:{
            //   验证用户名是否合法
            username:[
            { required: true, message: '请输入登录名称', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
            ],
            // 验证密码是否合法
            password:[
              { required: true, message: '请输入登录密码', trigger: 'blur' },
              { min: 6, max: 15, message: '长度在 6 到 10 个字符', trigger: 'blur' }
            ]
        }
      }
   },
   methods:{
       //点击重置按钮，重置登录表单
       resetLoginFrom(){
        //    console.log(this)
        this.$refs.loginFromRef.resetFields()
       },
       login(){
           this.$refs.loginFromRef.validate( async valid =>{
            //  console.log(valid)
            if(!valid) return;
            const {data : res} = await this.$http.post('login',this.loginFrom);
            //  console.log(res)
            if(res.meta.status !==200) return this.$message.error("登录失败！")
            this.$message.success("登录成功")
            // 1.将登陆成功之后的token保存到客户端的sessionStorage中
            //   1.1项目中除了登录之外的其他API接口，必须在登录之后才能访问
            // 1.2token 只应在当前网站打开期间生效，所以将token保存在sessionStorage中
           console.log(res)
           window.sessionStorage.setItem('token',res.data.token)
           // 2.通过编程式导航跳转到后台主页，路由跳转的地址是/home
           this.$router.push("/home")
           })
       }
   }
}
</script>

<style lang="less" scoped>
.login_container{
    background-color: #2b4b6b;
    height: 100%;
}
.login_box{
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    .avatar_box{
        background-color: #fff;
       width: 130px;
        height: 130px;
        border: 1px solid #eee;
        border-radius: 50%;
        padding: 10px;
        box-shadow: 0 0 10px #ddd;
        position: absolute;
        left: 50%;
        transform: translate(-50%,-50%);
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background: #eee;
        }
    }
}
.login_from{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 30px;
    box-sizing: border-box;
}
.btns{
    display: flex;
    justify-content: flex-end;
}
</style>